<template>
  <div class="checkStatistics">
    <div class="topHeader">
      <topNav title="勾兑统计" url="/checkIndex"></topNav>
    </div>
    <searchHeader
      class="searchHeader"
      :title="checkPs.taCodeName"
      v-model="searchValue"
      @search-form="search"
    ></searchHeader>
    <checkOverView :checkOverView="checkOverView"></checkOverView>

    <mt-navbar class="page-part" v-model="selected">
      <mt-tab-item id="unUse">本日可用</mt-tab-item>
      <mt-tab-item id="used">本日已用</mt-tab-item>
    </mt-navbar>

    <!-- tab-container -->
    <mt-tab-container v-model="selected">
      <mt-tab-container-item id="unUse">
        <div class="checkStatisticsList">
          <ul
            v-if="originalData.length>0 "
            v-infinite-scroll="originalDataLoadMore"
            infinite-scroll-distance="10"
          >
            <template v-for="(items,index) in originalData">
              <layoutV :key="index" :obj="items">
                <!-- <div class="checkStatisticsListFooter" slot="footer">
                  <mt-button class="checkStatisticsListButton useButton" size="small" plain>使用</mt-button>
                </div>-->
              </layoutV>
            </template>
            <div class="bottomLine" v-if="originalDataShowBottomLine">我是有底线的</div>
          </ul>
          <div class="noValue-container" v-else>
            <span class="noValue-icon iconfont icon-jiazaishibai"></span>
            <P class="noValue-TXT">{{noBreakfastMSG}}</P>
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="used">
        <div class="checkStatisticsList">
          <ul
            v-if="usedOriginalData.length>0"
            v-infinite-scroll="usedOriginalDataLoadMore"
            infinite-scroll-distance="10"
          >
            <layoutV v-for="(items,index) in usedOriginalData" :key="index" :obj="items">
              <!-- <div class="checkStatisticsListFooter" slot="footer">
                <mt-button class="checkStatisticsListButton useButton" size="small" plain>撤销</mt-button>
              </div>-->
            </layoutV>
            <div class="bottomLine" v-if="usedOriginalDataShowBottomLine">我是有底线的</div>
          </ul>
          <div class="noValue-container" v-else>
            <span class="noValue-icon iconfont icon-jiazaishibai"></span>
            <P class="noValue-TXT">{{noBreakfastMSG}}</P>
          </div>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>
import moment from "moment";
import { InfiniteScroll } from "mint-ui";
import { Toast, MessageBox } from "mint-ui";
import layoutV from "../common/layoutV";
import searchHeader from "../common/searchHeader";
import checkOverView from "../common/checkOverView";
export default {
  data() {
    return {
      checkPs: {
        taCodeName: "中餐厅"
      },
      selected: "used",
      checkOverView: {},
      style: {
        padding: "12px 14px 12px 11px",
        "box-shadow": "0 0 4px 0 rgba(0,0,0,0.1)",
        margin: "10px 0"
      },
      originalData: [],
      originalDataArr: [],
      usedOriginalData: [],
      usedOriginalDataArr: [],
      searchValue: "",
      noBreakfastMSG: "什么都没有",
      isNoBreakfast: false,
      originalDataShowBottomLine: false,
      usedOriginalDataShowBottomLine: false,
      arrIndex: 1
    };
  },

  components: {
    //layout,
    layoutV,
    searchHeader,
    checkOverView
  },
  mounted() {
    // var ps = localStorage.getItem("checkPs");
    // if (ps == null) {
    //   this.msg("请先设置参数");
    //   this.$router.push("/checkSet");
    // } else {
    //   this.checkPs = JSON.parse(ps);
    // }
    this.getBreakfastConsumeStatistics();
    this.getConsumptionReport();
  },
  methods: {
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    search(value) {
      if (value) {
        this.searchData(value);
      } else {
        this.getConsumptionReport();
      }
    },
    searchData(rmno) {
      let rsvDate = moment().format("YYYY-MM-DD");
      //let rsvDate = "2019-01-25";
      this.$http
        .get("/ohotel/breakfast/fetchValidBreakfastByRoom", {
          params: {
            condition: {
              roomNum: rmno,
              rsvDate: rsvDate
            }
          },
          post: true
        })
        .then(res => {
          this.originalData = [];
          this.usedOriginalData = [];
          if (res.data.success) {
            if (res.data.result.resrvs.length > 0) {
              this.originalDataShowBottomLine = false;
              this.usedOriginalDataShowBottomLine = false;
              res.data.result.resrvs.forEach((ele, index) => {
                if (ele.breakfastCount > ele.usedBreakfastCount) {
                  let obj = {
                    guestName: ele.guestName,
                    room: ele.roomNum,
                    num: ele.breakfastCount,
                    isAccount: ele.noPostFlag,
                    used: ele.usedBreakfastCount,
                    Surplus: ele.breakfastCount - ele.usedBreakfastCount,
                    arrivalTime: ele.checkInTime,
                    departureTime: ele.checkOutTime,
                    vipStatus: ele.vipStatus ? ele.vipStatus : "否"
                  };
                  this.originalData.push(obj);
                } else {
                  this.originalData = [];
                  this.isNoBreakfast = true;
                  this.noBreakfastMSG = "暂无可核销早餐";
                }
                if (ele.usedBreakfastCount > 0) {
                  let obj = {
                    guestName: ele.guestName,
                    room: ele.roomNum,
                    num: ele.breakfastCount,
                    isAccount: ele.noPostFlag,
                    used: ele.usedBreakfastCount,
                    Surplus: ele.breakfastCount - ele.usedBreakfastCount,
                    arrivalTime: ele.checkInTime,
                    departureTime: ele.checkOutTime,
                    vipStatus: ele.vipStatus ? ele.vipStatus : "否"
                  };
                  this.usedOriginalData.push(obj);
                } else {
                  this.usedOriginalData = [];
                  this.isNoBreakfast = true;
                  this.noBreakfastMSG = "暂无可核销早餐";
                }
              });
            } else {
              this.isNoBreakfast = true;
              this.noBreakfastMSG = res.data.msgs;
            }
          } else {
            this.isNoBreakfast = true;
            this.noBreakfastMSG = res.data.msgs;
          }
        });
    },
    getBreakfastConsumeStatistics() {
      this.$http
        .get("/ohotel/breakfast/fetchBreakfastConsumeStatistics")
        .then(res => {
          if (res.status == 200 || res.status == 204) {
            this.checkOverView = {
              guestsNum: res.data.result.guestCount,
              breakfastNum: res.data.result.totalCount
                ? res.data.result.totalCount
                : 0,
              writeOffsNum: res.data.result.usedCount
                ? res.data.result.usedCount
                : 0
            };
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    getConsumptionReport() {
      let data = new Date();
      let data2 =
        data.toString().substring(0, 15) +
        " 00:00:00 " +
        data.toString().substring(25);
      this.$http
        .get(
          "/ohotel/breakfast/fetchConsumptionReport?bizDate=" +
            encodeURIComponent(data2),
          {
            dpost: true
          }
        )
        .then(res => {
          if (res.status == 200 || res.status == 204) {
            if (res.data.success) {
              this.originalData = [];
              this.usedOriginalData = [];
              if (res.data.result) {
                if (res.data.result.length > 10) {
                  res.data.result.forEach(ele => {
                    if (ele.usedBreakfastCount > 0) {
                      let obj = {
                        guestName: ele.guestName,
                        room: ele.roomNum,
                        num: ele.breakfastCount,
                        isAccount: ele.noPostFlag,
                        used: ele.usedBreakfastCount,
                        Surplus: ele.breakfastCount - ele.usedBreakfastCount,
                        arrivalTime: ele.checkInTime,
                        departureTime: ele.checkOutTime,
                        vipStatus: ele.vipStatus ? ele.vipStatus : "否"
                      };
                      this.usedOriginalDataArr.push(obj);
                    }
                    if (ele.breakfastCount > ele.usedBreakfastCount) {
                      let obj = {
                        guestName: ele.guestName,
                        room: ele.roomNum,
                        num: ele.breakfastCount,
                        isAccount: ele.noPostFlag,
                        used: ele.usedBreakfastCount,
                        Surplus: ele.breakfastCount - ele.usedBreakfastCount,
                        arrivalTime: ele.checkInTime,
                        departureTime: ele.checkOutTime,
                        vipStatus: ele.vipStatus ? ele.vipStatus : "否"
                      };
                      this.originalDataArr.push(obj);
                    }
                  });
                  this.originalData.push(...this.originalDataArr.slice(0, 10));
                  this.usedOriginalData.push(
                    ...this.usedOriginalDataArr.slice(0, 10)
                  );
                } else {
                  res.data.result.forEach(ele => {
                    if (ele.usedBreakfastCount > 0) {
                      let obj = {
                        guestName: ele.guestName,
                        room: ele.roomNum,
                        num: ele.breakfastCount,
                        isAccount: ele.noPostFlag,
                        used: ele.usedBreakfastCount,
                        Surplus: ele.breakfastCount - ele.usedBreakfastCount,
                        arrivalTime: ele.checkInTime,
                        departureTime: ele.checkOutTime,
                        vipStatus: ele.vipStatus ? ele.vipStatus : "否"
                      };
                      this.usedOriginalData.push(obj);
                    }
                    if (ele.breakfastCount > ele.usedBreakfastCount) {
                      let obj = {
                        guestName: ele.guestName,
                        room: ele.roomNum,
                        num: ele.breakfastCount,
                        isAccount: ele.noPostFlag,
                        used: ele.usedBreakfastCount,
                        Surplus: ele.breakfastCount - ele.usedBreakfastCount,
                        arrivalTime: ele.checkInTime,
                        departureTime: ele.checkOutTime,
                        vipStatus: ele.vipStatus ? ele.vipStatus : "否"
                      };
                      this.originalData.push(obj);
                    }
                  });
                }
              }
            } else {
              this.msg(res.data.msgs);
            }
          }
        });
    },
    originalDataLoadMore() {
      let last = this.originalData.length - 1;
      for (let i = 1; i <= 10; i++) {
        if (this.originalData.length < this.originalDataArr.length) {
          this.originalData.push(this.originalDataArr[last + i]);
        } else {
          if (last > 2) {
            this.originalDataShowBottomLine = true;
          }
        }
      }
    },
    usedOriginalDataLoadMore() {
      let last = this.usedOriginalData.length - 1;
      for (let i = 1; i <= 10; i++) {
        if (this.usedOriginalData.length < this.usedOriginalDataArr.length) {
          this.usedOriginalData.push(this.usedOriginalDataArr[last + i]);
        } else {
          if (last > 2) {
            this.usedOriginalDataShowBottomLine = true;
          }
        }
      }
    }
  }
};
</script>

<style lang="less">
.checkStatistics {
  .mint-tab-item {
    .mint-tab-item-label {
      height: 38px;
      line-height: 3;
    }
  }
}
</style><style lang="less" scoped>
.checkStatistics {
  height: 100vh;
  overflow: auto;
  background: #f4f5f7;

  .searchHeader {
    margin-bottom: 15px;
  }

  .checkStatisticsList {
    margin: 10px 8px 0;

    .checkStatisticsListFooter {
      padding: 12px 0 7px;
      text-align: right;
      margin-top: 16px;
      border-top: 1px solid #eeeeee;
    }

    .checkStatisticsListButton {
      width: 60px;
      height: 25px;
      border-radius: 2px;
      border: none;
      color: #fff;
    }

    .repealButton {
      background: #d9d9d9;
    }

    .throwAccountButton {
      background: #d8af68;
    }

    .useButton {
      background: #ff8902;
    }

    .noValue-container {
      margin-top: 50%;

      .noValue-icon {
        font-size: 48px;
      }

      .noValue-TXT {
        font-size: 14px;
        color: #333333;
      }
    }

    .bottomLine {
      padding: 10px 0;
    }
  }

  .page-part {
    margin-top: 5px;

    .mint-tab-item {
      padding: 0px 0;
    }

    .mint-tab-item.is-selected {
      color: #646f83;
      border-bottom: 2px solid #646f83;
      margin-bottom: 0;
    }
  }
}
</style>



