<template>
  <div class="searchHeader">
    <div class="search">
      <span @click="SwipeFun">
        <Icon class="iconfont icon-roomNumber" name="ganyingqi"></Icon>
      </span>
      <div class="searchInput">
        <input type="text" :value="searchInput" @input="onInput" placeholder="请输入房号">
      </div>
      <div class="searchBtnBox">
        <button class="searchBtn" @click="searchFrom">搜索</button>
      </div>
    </div>
  </div>
</template>
<script>
import Icon from "./icon";
export default {
  props: {
    searchInput: String
  },
  model: {
    prop: "searchInput",
    event: "inputx"
  },
  components: {
    Icon
  },
  methods: {
    searchFrom() {
      this.$emit("search-form", this.searchInput);
    },
    onInput(e) {
      this.$emit("inputx", e.target.value);
    },
    SwipeFun() {
      this.$emit("SwipeFun");
    }
  }
};
</script>
<style lang="less" scoped>
.searchHeader {
  .search {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 0.35rem;
    .icon-roomNumber {
      color: #646f83;
      height: 22px;
      padding: 0 9px;
      width: 46px;
    }
    .searchInput {
      position: relative;
      flex: 1;
      height: 30px;
      input {
        box-sizing: border-box;
        width: 100%;
        border: 1px solid #cccccc;
        padding: 0 9px;
        border-radius: 5px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        outline: none;
      }
    }
    .searchBtnBox {
      width: 60px;
      height: 30px;
      padding: 0 9px 0 16px;
      .searchBtn {
        width: 100%;
        height: 100%;
        border: none;
        color: #fff;
        background: #f9c203;
        background: #d8af68;
        border-radius: 7px;
        padding: 0;
        margin: 0;
        outline: none;
      }
    }
  }
}
</style>




