<template>
<div class="checkOverview">
    <!-- <P>总数：
      <span>{{checkOverView}}</span>
    </P>
    <P>已使用：
      <span>{{checkOverView}}</span>
    </P>
    <P>本日使用：
      <span>{{checkOverView}}</span>
    </P>
    <P>剩余：
      <span>{{checkOverView}}</span>
    </P>-->
    <p>
        共
        <span>{{checkOverView.guestsNum}}</span> 位含早宾客，计
        <span>{{checkOverView.breakfastNum}}</span> 份早餐，已核销
        <span>{{checkOverView.writeOffsNum}}</span> 份
    </p>
</div>
</template>

<script>
export default {
    props: {
        checkOverView: Object
    }
};
</script>

<style lang="less" scoped>
.checkOverview {
    display: flex;
    justify-content: left;
    align-items: center;
    height: 45px;
    background: #ffffff;
    padding: 0 8px;

    p {
        font-size: 0.33rem;
        color: #999;

        span {
            font-size: 0.4rem;
            color: #ce0808;
        }
    }
}
</style>



