<template>
  <div class="layout">
    <i class="icon"></i>
    <div class="header">
      <div class="flex50">
        <p>
          宾客：
          <span>{{obj.guestName}}</span>
        </p>
      </div>
      <div class="flex50">
        <p>
          VIP ：
          <span>{{obj.vipStatus}}</span>
        </p>
      </div>
      <div class="flex50">
        <p>
          份数：
          <span>{{obj.num}}</span>
        </p>
      </div>
      <div class="flex50">
        <p>
          房间：
          <span>{{obj.room}}</span>
        </p>
      </div>
      <div class="flex50">
        <p>
          挂帐：
          <span>{{obj.isAccount}}</span>
        </p>
      </div>
      <div class="flex50">
        <p>
          已用：
          <span>
            {{obj.used}}
            <small style="font-size:80%">份</small>
          </span>
        </p>
      </div>
      <div class="flex50">
        <p>
          剩余：
          <span style="color:red">
            {{obj.Surplus}}
            <small style="font-size:80%">份</small>
          </span>
        </p>
      </div>
      <div class="flex50">
        <p>
          超用：
          <span style="color:red">
            {{obj.additionalCount}}
            <small style="font-size:80%">份</small>
          </span>
        </p>
      </div>
      <div class="flex50">
        <p>
          到店：
          <span>{{obj.arrivalTime | fd}}</span>
        </p>
      </div>
      <div class="flex50">
        <p>
          离店：
          <span>{{obj.departureTime | fd}}</span>
        </p>
      </div>
      <div v-if="obj.hasOwnProperty('writeOffTime')" class="flex100">
        <p>
          核销：
          <span>{{obj.writeOffTime}}</span>
        </p>
      </div>
    </div>
    <slot name="footer"></slot>
  </div>
</template>

<script>
import moment from "moment";
export default {
  props: {
    obj: Object
  },
  filters: {
    fd(value) {
      if (value == null || value == "") return "";
      return moment(value).format("MM/DD");
    }
  }
};
</script>

<style lang="less" scoped>
.layout {
  position: relative;
  background: #fff;
  border-radius: 5px;
  padding: 18px 14px 8px 11px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0 4px 0;
  margin: 10px 0px;

  .header {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;

    .flex100 {
      flex-basis: 100%;

      p {
        font-size: 12px;
        color: #999999;
        text-align: left;

        span {
          font-size: 0.33rem;
          color: #151515;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }

    .flex50 {
      flex-basis: 50%;

      p {
        font-size: 12px;
        color: #999999;
        text-align: left;

        span {
          font-size: 0.33rem;
          color: #151515;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }

  .icon {
    position: absolute;
    top: 15px;
    left: 0;
    width: 3px;
    height: 12px;
    background: #646f83;
  }
}
</style>



